import React, { useState } from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import Google from '../images/google.png'
import facebook from '../images/facebook.png'
import Logo from '../images/logo.png'
import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input'
import { formatPhoneNumberIntl } from 'react-phone-number-input'

import {
  useNavigate
} from 'react-router-dom';


import { Link } from "react-router-dom";


import axios from 'axios';

const Signup=()=>{

  const navigate = useNavigate();
    const [formValue, setformValue] = React.useState({
        name:'',
        email: '',
        password: '',
        password2:'',
        phone:'',
        country_code:''
      });
      
      const [value, setValue] = React.useState('')
      let gender=''
      const handleSubmit=async(event) => {
        event.preventDefault();
        const loginFormData = new FormData();
        loginFormData.append("name", formValue.name)
        loginFormData.append("email", formValue.email)
        loginFormData.append("password", formValue.password)        
        loginFormData.append("password2", formValue.password2)
        loginFormData.append("phone", value.replace(formatPhoneNumberIntl(value).split(' ',1)[0],''))
        loginFormData.append("country_code", formatPhoneNumberIntl(value).split(' ',1)[0])
        loginFormData.append("gender", gender)     
        try {
          const response = await axios({
            method: "post",
            url: "http://127.0.0.1:8000/account/signup/",
            data: loginFormData,
            headers: { "Content-Type": "multipart/form-data" },
          }).then((response)=>{alert(response.data.msg)
            navigate("/");})
        } catch(error) {
          console.log(error)
        }
      }
      const [checked, setChecked] = useState("");
      const [sechecked, setSechecked] = useState("");
      gender=sechecked
      const [maleCheck, updatemaleCheck] = useState(0);
      const [femaleCheck, updatefemaleCheck] = useState(0);
      if(maleCheck){
          gender=checked
      }
      else{
        gender=sechecked
    }
      const handleChange = (event) => {
        setformValue({
          ...formValue,
          [event.target.name]: event.target.value
        });
      }
    return(
        <Container className='my-5'>
            <Row>
                <Col style={{textAlign:"center",background:'#ffadad',border:"1px white solid", padding:"10px", borderTopLeftRadius:"15px",borderBottomLeftRadius:"15px"}}>
                <h2 className='mt-4'>Create An Account</h2>
                    <Form onSubmit={handleSubmit} className="mx-5 my-5">
                      <Container>
                        <Row>
                          <Col>
                                      <Form.Group className="mb-3">
                                          <Form.Control required type="name" placeholder="FullName" name="name" value={formValue.name} onChange={handleChange}/>
                                      </Form.Group>
                          </Col>
                          <Col>
                                      <Form.Group className="mb-3">
                                          <Form.Control required type="email" placeholder="Email" name="email" value={formValue.email} onChange={handleChange}/>
                                      </Form.Group>
                          </Col>
                        </Row>
                        <Row>
                          <Col>
                                      <Form.Group className="mb-3">
                                          <Form.Control required type="password" placeholder="Password" name="password" value={formValue.password} onChange={handleChange}/>
                                      </Form.Group>
                          </Col>
                          <Col>

                                      <Form.Group className="mb-3">
                                          <Form.Control required type="password" placeholder="Confirm Password" name="password2" value={formValue.password2} onChange={handleChange}/>
                                      </Form.Group>
                            </Col>
                        </Row>
                        <Row>
                          <Col md={6} lg={6} className='my-2'>
                          <PhoneInput
                              required
                              defaultCountry="IN"
                              value={value}
                              name="PhNo" 
                              placeholder="Phone Number"
                              onChange={setValue}
                              style={{
                                borderRadius:"15px"
                              }}/>
                          </Col>
                          <Col>
                                <Form style={{backgroundColor:"white", borderRadius:"10px"}}>
                                <p style={{padding:"0px", marginBottom:".1px"}}>Gender</p>
                                    <div className="mb-3">
                                        <Form.Check
                                          inline
                                          required
                                          label="Male"
                                          name="male"
                                          value={checked}
                                          checked={maleCheck}
                                          onChange={(e) => {setChecked('Male'); updatemaleCheck(1);updatefemaleCheck(0)}}
                                        />
                                        <Form.Check
                                          inline
                                          required
                                          label="Female"
                                          name="female"
                                          value={sechecked}
                                          checked={femaleCheck}
                                          onChange={(e) => {
                                          {setSechecked('Female'); updatemaleCheck(0);updatefemaleCheck(1)}
                                          }}
                                        />
                                      </div>
                                  </Form>
                          </Col>
                        </Row>
                        
                                      <Button variant="danger" type="submit" style={{width:"100%"}} className="mt-4">
                                          Sign Up
                                      </Button>
                                      <p className='mt-3'> Already have an Account? <Link to="/" style={{textDecoration:'None',color:"red"}}>Sign in</Link><br/><br/>Or sign in with</p>
                        </Container>
                    </Form>
                    <Container className='mt-3'>
                        <Row>
                            <Col className='mx-3'>
                            <img src={Google} width="40" height="40" style={{float:'right',background:'white',padding:'5px',borderRadius:'10px'}}/>
                            </Col>
                            <Col className='mx-3'>
                            <img src={facebook} width="40" height="40" style={{float:'left',background:'white',padding:'5px',borderRadius:'10px'}}/>
                            </Col>
                        </Row>
                    </Container>
                </Col>
                <Col style={{textAlign:"center",background:'#ff7171',border:"1px white solid", padding:"10px", borderTopRightRadius:"15px",borderBottomRightRadius:"15px"}}>
                    <img src={Logo} width="495px" height="400px"/>
                </Col>
            </Row>
        </Container>
    )
}


export default Signup