import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import "../App.css"
import { useState } from 'react';
import { Col, Container, Row , Image,Navbar,Nav} from 'react-bootstrap';
import All from '../images/all.png'
import Video from '../images/Video.png'
import Read from '../images/Read.png'
import PageLogo from '../images/logo.png'
import { Link } from "react-router-dom";
import DropdownImage from './dropdownIcon';
function TopNav() {
    const [btn1,updateBtn1]=useState(false)
    const [btn2,updateBtn2]=useState(false)
    const [btn3,updateBtn3]=useState(false)
    const UpdateBtn1=()=>{
        updateBtn1(!btn1)
        updateBtn2(false)
        updateBtn3(false)
    }
    const UpdateBtn2=()=>{
        updateBtn1(false)
        updateBtn2(!btn2)
        updateBtn3(false)
    }
    const UpdateBtn3=()=>{
        updateBtn1(false)
        updateBtn2(false)
        updateBtn3(!btn3)
    }
  return (
    <Container className='mt-3'>
        <Row>
            <Col md={2} lg={2} xl={2}><Link to="/home"><Image src={PageLogo} fluid/></Link></Col>
            <Col className='mt-3'>
                <ButtonGroup aria-label="Basic example" style={{boxShadow:"2px 2px 10px grey",width:"90%",height:"70px"}}>


                <Button variant="white" className="buttonHover" onClick={UpdateBtn1} style={{backgroundColor:btn1?"#ffadad":"white"}}><Link to="/home" style={{textDecoration:"None"}}><Image src={All} width="auto" height="auto" style={{padding:'10px'}}/>All</Link></Button>


                <Button variant="white" className="buttonHover" onClick={UpdateBtn2} style={{backgroundColor:btn2?'#ffadad':'white'}}><Link to="/post" style={{textDecoration:"None"}}><Image src={Read} width="auto" height="auto" style={{padding:'10px'}}/>Read</Link></Button>

                
                <Button variant="white" className="buttonHover" onClick={UpdateBtn3} style={{backgroundColor:btn3?'#ffadad':'white'}}><Link to="/video" style={{textDecoration:"None"}}><Image src={Video} width="auto" height="auto" style={{padding:'10px'}}/>Video</Link></Button>
                </ButtonGroup>
            </Col>
            <Col md={2} lg={2} xl={2} className='mt-3'><DropdownImage/></Col>
            {/* <Image src={user} fluid style={{float:"right"}}/> */}
        </Row>
        <Row className="mt-2">
            <Col>
                <Navbar collapseOnSelect expand="lg" bg="white" variant="white">
                <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Container className="mt-2 d-flex justify-content-xl-center mx-auto">
                        <Nav>
                            <Nav.Link href="#a" className='mx-2'>Home</Nav.Link>
                            <Nav.Link href="#b" className='mx-2'>Latest</Nav.Link>
                            <Nav.Link href="#1" className='mx-2'>News</Nav.Link>
                            <Nav.Link href="#2" className='mx-2'>Sports</Nav.Link>
                            <Nav.Link href="#3" className='mx-2'>Election</Nav.Link>
                            <Nav.Link href="#4" className='mx-2'>Entertainment</Nav.Link>
                            <Nav.Link href="#6" className='mx-2'>Technology</Nav.Link>
                            <Nav.Link href="#5" className='mx-2'>Business</Nav.Link>
                            <Nav.Link href="#7" className='mx-2'>Education</Nav.Link>
                            <Nav.Link href="#8" className='mx-2'>Special</Nav.Link>
                        </Nav>
                    </Container>
                </Navbar.Collapse>
                </Navbar>
            </Col>
        </Row>
    </Container>
    
  );
}

export default TopNav;