import React, { useState } from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import Google from '../images/google.png'
import facebook from '../images/facebook.png'
import Logo from '../images/logo.png'
import { GoogleLogin } from '@react-oauth/google';
import axios from 'axios';
import {
  useNavigate
} from 'react-router-dom';

import { Link } from "react-router-dom";

const ErrorLogginIn=(props)=>{
  if (props.err=='None')
  {
    return(<p></p>)
  }
  else if (props.err.response.statusText=="Bad Request")
  {
    return(
      <p className='mt-0' style={{fontSize:'12px' ,color:'red'}}>{props.err.response.data.message}</p>
    )
  }
}

const Login=(props)=>{

  const navigate = useNavigate();
    const [formValue, setformValue]=React.useState({
        email:"",
        password:""
    })

    const [loading, setLoading] = useState(false);
    const [displayError, UpdatedisplayError] = useState('None');
    const handleSubmit=async(event) => {
        event.preventDefault();
        const loginFormData = new FormData();
        loginFormData.append("email", formValue.email)
        loginFormData.append("password", formValue.password)
        try {
          const response = await axios({
            method: "post",
            url: "http://127.0.0.1:8000/account/login/",
            data: loginFormData,
            headers: { "Content-Type": "multipart/form-data" },
          }).then((response)=>{
            setLoading(false);
            sessionStorage.setItem('token', response.data.content.token);
            sessionStorage.setItem('user', JSON.stringify(response.data.content.name));
            sessionStorage.setItem('email', JSON.stringify(response.data.content.email));
            navigate("/home");
          })
        } catch(error) {
          UpdatedisplayError(error)
        }
      }
    const handleChange = (event) => {
        event.preventDefault();
        setformValue({
          ...formValue,
          [event.target.name]: event.target.value
        });
      }
      const handleClick = () => {
        window.location.replace('http://127.0.0.1:8000/account/password_reset/');
      };
    const responseMessage = (response) => {
      console.log(response);
  };
  const errorMessage = (error) => {
      console.log(error);
  };
    return(
        <Container className='mt-5 pt-5 justify-content-md-center row' style={{verticalAlign:"center"}}>
            <Row>
                <Col style={{textAlign:"center",background:'#ffadad',border:"1px white solid", padding:"10px", borderTopLeftRadius:"15px",borderBottomLeftRadius:"15px"}} >
                    <h1>Sign In</h1>
                    <Form onSubmit={handleSubmit} className="mx-5 my-5">
                    <Form.Group className="mb-3">
                        <Form.Control type="email" required placeholder="Email" name="email" value={formValue.email} onChange={handleChange}/>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Control type="password" required placeholder="Password" name="password" value={formValue.password} onChange={handleChange}/>
                    </Form.Group>
                    <p style={{float:'right', fontSize:'12px'}}><a onClick={handleClick}>Forgot Password</a></p>
                    <ErrorLogginIn err={displayError}/>
                    <Button variant="success" type="submit" style={{width:"100%"}} className="mt-2">
                        Sign in
                    </Button>
                    <Button variant="warning" style={{width:"100%"}} className="mt-4"> <Link to="/signup" style={{textDecoration:"None",color:"white",fontWeight:"bold"}}>Sign up </Link></Button>
                    <p className='mt-3'> Or sign in with</p>
                    </Form>
                    <Container>
                        <Row>
                        <Col></Col>
                            <Col>
                            {/* <img src={Google} width="40" height="40" style={{float:'right',background:'white',padding:'5px',borderRadius:'10px'}}/><GoogleLogin onSuccess={responseMessage} shape="square" onError={errorMessage} /> */}

                            <div className='spacer'></div>
                              <GoogleLogin
                                onSuccess={credentialResponse => {
                                  console.log(credentialResponse);
                                }}
                                type="icon"
                                shape="square"
                                onError={() => {
                                  console.log('Login Failed');
                                }}
                                />
                            </Col>
                            <Col style={{float:"left"}}>
                            <img src={facebook} width="40" height="40" style={{background:'white',padding:'5px',borderRadius:'10px'}}/>
                            {/* <FacebookLoginComponent/> */}
                            </Col>
                        </Row>
                    </Container>
                </Col>
                <Col style={{textAlign:"center",background:'#ff7171',border:"1px white solid", padding:"10px", borderTopRightRadius:"15px",borderBottomRightRadius:"15px"}}>
                    <img src={Logo} width="495px" height="400px"/>
                </Col>
            </Row>
            <Row>
              <Col><Link to="/home" style={{textDecoration:"None",color:"red",textAlign:"center"}}><p>Continue without signup</p></Link></Col>
            </Row>
        </Container>
    )
}


export default Login