import React, { useEffect, useState } from 'react'
import {Container,Col,Row,Image,Carousel,Card} from 'react-bootstrap'
import features from '../images/fatures.png'
import Mg from '../images/background.jpg'
import '../App.css'
import TopNav from './TopNav';

import { Link } from "react-router-dom";
import axios from 'axios';

const Posts=(props)=>{
    let news=props['data']

    const user = sessionStorage.getItem('user');
    return(
        <Card>
            <Card.Body style={{height:'210px'}}>
                <Container>
                    <Row>
                        <Col xl={5}><Image variant="top" src={`http://127.0.0.1:8000/media/${news['image']}`} fluid/></Col>
                        <Col>
                        <Link to={`/post/${news.id}`} state={{data:{news}}}>
                        <span><p style={{fontSize:'20px'}}>{news['title']}</p></span><span><p style={{fontSize:'13px', color:'grey'}}>{news['news'].slice(0,150)}</p></span>
                        </Link>
                        </Col>
                    </Row>
                </Container>
            </Card.Body>
          </Card>
    )
}
const Crousal=(props)=>{
    let news=props['data']
    return(
                        <Container style={{height:'200px'}}>
                            <Row>
                                <Col xl={3}><Image
                                src={`http://127.0.0.1:8000/media/${news['image']}`}
                                fluid
                                alt="First slide" className='m-2'
                                /></Col>
                                <Col className='d-flex justify-content-center mx-auto' ><span>
                                <h3 style={{color:"black"}}>{news.title}</h3>
                                <p style={{color:"black"}}>{news.news.slice(0,150)}..Read more</p></span></Col>
                            </Row>
                        </Container>
    )
}
const Blog=()=>{
    const [news, setPosts] = useState([]);

    useEffect(() => {
      axios.get('http://127.0.0.1:8000/news/')
      .then(res => setPosts(res.data.content))
      .catch(err => console.log(err));
    }, [])
    return(
        <>
        <Container>
            <Row><TopNav/></Row>
        </Container>

        <Container className='mt-5' style={{boxShadow:"2px 2px 5px grey",borderRadius:"10px",backgroundColor:"#f3f4f6"}}>
            <Row className='p-1'>
                <Col className='d-flex justify-content-center mx-auto mt-3 p-1' style={{backgroundColor:"#ffadad", borderRadius:'10px',color:"white"}}><h2><Image src={features} fluid/><span className='m-3'>Featured</span></h2></Col>
            </Row>


            <Row style={{backgroundColor:"white",borderRadius:"10px"}} className="m-5">
                <Col>

            <Carousel variant="dark" controls={false}>
                {news.map(items=>
                <Carousel.Item className='p-3' key={items.id}>
            
                    <Link to={{ pathname: `/post/${items.id}`, data: { dataa: items } }} style={{textDecoration:'none'}}><Crousal data={items}/></Link>
                
                </Carousel.Item>)}
            </Carousel>
                </Col>
            </Row>
            
    <Row xs={1} md={2} className="g-4 m-2">
    {news.map(items=><Col key={items.id}><div className='p-2'>
          
    <Link to={{ pathname: `/post/${items.id}`, data: { dataa: items } }}><Posts data={items}/></Link>
        </div></Col>)}
    </Row>
        </Container>
        </>
    )
}

export default Blog