import React, { useEffect, useState } from "react";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import TopNav from './TopNav';
import axios from 'axios';

function Itemcards(props){
  let posts=props['data']
  return(
      <>
      <iframe width="560" height="315" src={"https://www.youtube.com/embed/"+posts["link"].split('/').pop()} allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowFullScreen
      title={posts['video_description']}></iframe>

        
      <h5>{posts['video_description']}</h5>
      </>
  )
}

function Video() {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    axios.get('http://127.0.0.1:8000/news/video/')
    .then(res => setPosts(res.data.content))
    .catch(err => console.log(err));
  }, [])
  return (
    <>
        <Container>
            <Row><TopNav/></Row>
        </Container>
    <Container className='pt-3' style={{boxShadow:"2px 2px 5px grey",borderRadius:"10px",backgroundColor:"#f3f4f6"}}>
      <Row >
          {posts.map(items=><Col key={items.id}><div className='p-2 text-center'>
          <Itemcards data={items}/>
        </div></Col>)}
      </Row>
    </Container>
    </>
  );
}

export default Video;